	import {api} from	'./api.js'
    export default {
        data() {
            return {
                SessionKey: '',
                OpenId: '',
                nickName: null,
                avatarUrl: null,
                isCanUse: uni.getStorageSync('isCanUse')||true//默认为true
            };
        },
        methods: {
            //第一授权获取用户信息===》按钮触发
            wxGetUserInfo() {
				console.log('...授权...')
                let _this = this;
                uni.getUserInfo({
                    provider: 'weixin',
                    success: function(infoRes) {
                        let nickName = infoRes.userInfo.nickName; //昵称
                        let avatarUrl = infoRes.userInfo.avatarUrl; //头像
						console.log(nickName)
						console.log(avatarUrl)
                        try {
                            uni.setStorageSync('isCanUse', false);//记录是否第一次授权  false:表示不是第一次授权
                            _this.updateUserInfo();
                        } catch (e) {}
                    },
                    fail(res) {
						uni.showModal({
							content: '失败了',
							showCancel: true
						});
					}
                });
            },

　　　　　　//登录
                login() {
                let _this = this;
                uni.showLoading({
                    title: '登录中...'
                });
             
               // 1.wx获取登录用户code
                uni.login({
                    provider: 'weixin',
                    success: function(loginRes) {
                        let code = loginRes.code;
						console.log(loginRes)
                        if (!_this.isCanUse) {
                            //非第一次授权获取用户信息
                            uni.getUserInfo({
                                provider: 'weixin',
                                success: function(infoRes) {
 　　　　　　　　　　　　　　　　　　　　　　//获取用户信息后向调用信息更新方法
                                    let nickName = infoRes.userInfo.nickName; //昵称
                                    let avatarUrl = infoRes.userInfo.avatarUrl; //头像
                                        _this.updateUserInfo();//调用更新信息方法
                                }
                            });
                        }
            
                        //2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息
                        uni.request({
                            url: '服务器地址',
                            data: {
                                code: code,
                            },
                            method: 'GET',
                            header: {
                                'content-type': 'application/json'
                            },
                            success: (res) => {
                                //openId、或SessionKdy存储//隐藏loading
                                uni.hideLoading();
                            }
                        });
                    },
                });
            },
         //向后台更新信息
            updateUserInfo() {
                let _this = this;
				console.log('请求后台');
				let p = {
					currentPage: 1,
					pageSize: 10
				};
				api.getData(p).then(res=>{
					console.log(res.data)
				}).catch(err => {
					console.log(err)
				})
            }
        },
    }
